@for $i from 0 through 20 {
	$m: $i * 5;

	.mgb-#{$m}{
		margin-bottom: #{$m}px;
	}

	.mgt-#{$m}{
		margin-top: #{$m}px;
	}

	.mgl-#{$m}{
		margin-left: #{$m}px;
	}

	.mgr-#{$m}{
		margin-right: #{$m}px;
	}

	.pdb-#{$m}{
		padding-bottom: #{$m}px;
	}

	.pdt-#{$m}{
		padding-top: #{$m}px;
	}

	.pdl-#{$m}{
		padding-left: #{$m}px;
	}

	.pdr-#{$m}{
		padding-right: #{$m}px;
	}

}

#msg-ie{
	.alert{
		border-radius:0;
	}
}

.no-roundnnes{
	border-radius:0;
}

.internas{
	padding-bottom:140px;
}

.btn-enviar,
.btn-voltar{
	font-size:20px;
	min-width:190px;
}

a[data-lightbox]{
	cursor: zoom-in;
}